import emailjs from "@emailjs/browser";

//const SERVICE_ID = "service_5n2j19n";
const SERVICE_ID = "service_k0dneyb";
const TEMPLATE_ID = "template_jimacdr";
const PUBLIC_KEY = "BF18mawMdZZ2Qjej6";

    export async function sendVerificationEmail(toEmail, toName, code, name) {
        const params = {
            to_email: toEmail,
            to_name: toName,
            code: code,
            name: name
        };

        try {
            const response = await emailjs.send(SERVICE_ID, TEMPLATE_ID, params, PUBLIC_KEY);
            console.log(" Email enviado:", response.status, response.text);
            return true;
        } catch (error) {
            console.error(" Error al enviar email:", error);
            return false;
        }
    }